body {
  margin: 0;
  font-family: Georgia, 'Times New Roman', Times, serif;
  background-color: rgb(59, 52, 52);
  background: url('https://images.unsplash.com/photo-1534796636912-3b95b3ab5986?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8MXx8fGVufDB8fHx8&w=1000&q=80');
  /* background: url('https://images.squarespace-cdn.com/content/v1/5c0f697e9d5abb8c65cd6857/1599032187414-92U41D7HVIIFVCHHBDA3/Artboard+2.png?format=1000w'); */
  /* background: url('https://64.media.tumblr.com/d6d0246ca0dffa230f03be2521b3210f/tumblr_obfjz6Bd9f1runoqyo2_r1_540.gif'); */
  /* background: url('https://media.tenor.com/TLsSaQMZYNsAAAAd/stars-space.gif'); */
  /* https://media.tenor.com/TLsSaQMZYNsAAAAd/stars-space.gif */
  /* background-size: cover; */
  background-attachment: fixed;
  /* background-repeat: no-repeat; */

  /* VARIABLES */

  --background-general: black;

  --header-background: black;

  /* --background-find-container: rgb(29, 29, 36); */
  --background-find-container: rgba(0, 0, 0, 0);

  --character-textbox-first: rgba(0, 0, 0, 0);
  --character-textbox-second: rgb(4, 13, 31);

  --character-button-first: rgba(0, 0, 0, 0);
  --character-button-second: rgb(4, 13, 31);

  --button-color: rgb(255, 255, 255);
  --finder--border: white;

  /* --character-box-first: rgb(15,15,50);
  --character-box-second: gray; */
  --character-box-first: rgba(0, 0, 0, 0);
  --character-box-second: rgb(5, 5, 19);

  
  --character--name-color: white /* black */;

  --character--withInfo--name-color: white;
  --character--withInfo--fullname-color: white;
  --character--withInfo--alignment-color: white;

  /* --character--withInfo-background: rgba(101, 101, 101, 0);*/
  --character--withInfo-background: rgba(7, 0, 0, 0);

  --character--withInfo--statSelectors-background: rgb(3, 3, 8);
  --statSelectors-color: rgb(23, 49, 85);
  --statSelectors-selected-color: rgb(33, 87, 248);

  /* --character--withInfo--info-background: rgb(16, 14, 43);  */
  --character--withInfo--info-background: rgba(16, 14, 43, 0.342);

  --stat-info-color: white;
}




/* HEADER */
.header{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--header-background);
  color: white;
  top: 0;
  width: 100%;
  overflow: hidden;
}

.header--logo{
  height: 35px;
}

.header--text{
  margin-left: 10px;
}
/* HEADER */


/* FILTER  */
.find-container{
  width: 100%;
  display: flex;
  justify-content:center;
  height: 5vh;
  padding: 2rem 0;
  background-color: var(--background-find-container);
  border-radius: 0.5rem;
  gap: 1rem;
}

.find-by-name{
  font-size: 2vw;
  /* border-radius: 0.5rem; */
  background: linear-gradient(var(--character-textbox-first), var(--character-textbox-second));
  color: var(--button-color);
  border: none;
  border-bottom: 2px solid white;
}


.character--button{
  font-size: 2vw;
  border-radius: 0.5rem;
  background: linear-gradient(var(--character-button-first), var(--character-button-second));
  color: var(--button-color);
  border: none;
  /* border: 1px solid var(--finder--border); */
}

.input-howMany{
  font-size: 1.5vw;
  /* border-radius: 0.5rem; */
  background: linear-gradient(var(--character-textbox-first), var(--character-textbox-second));
  color: var(--button-color);
  border: none;
  /* border: 1px solid var(--finder--border); */
  border-bottom: 2px solid white;
}

.select-category{
  font-size: 1.5vw;
  border-radius: 0.5rem;
  background: linear-gradient(var(--character-textbox-first), var(--character-textbox-second) 75%);
  color: var(--finder--border);
  border: none;
  /* border: 1px solid var(--finder--border); */
}
option{
  color: black;
}
option[value="All"]{
  color: black;
}
option[value="good"]{
  color: black;
}
option[value="bad"]{
  color: black;
}
option[value="neutral"]{
  color: black;
}
option[value="Marvel Comics"]{
  color: rgb(165, 24, 24);
}
option[value="DC Comics"]{
  color: rgb(14, 12, 110);
}
/* FILTER  */



/* CHARACTERS LIST */
.characters--container{
  display: grid;
  grid-template-columns: repeat(3, 20vw);
  justify-content: center;
  margin-top: 2rem;
  gap: 1rem;
  padding-bottom: 5rem;
}

.character{
  background: linear-gradient(var(--character-box-first), var(--character-box-second));
  height: 100%;
  width: 100%;
  text-align:center;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  cursor: pointer;
}

.character--img{
  /* height: 70%; */
  /* width: 70%;
  height: auto;
  margin: 0 auto;
  margin-top: 1.5rem;*/
 
  border-radius: 0.5rem; 
  width: 100%;
  height: 100%;
  /* height: auto; */
}

/* .character:hover{
  animation-name: rainbow;
  animation-iteration-count: infinite;  
  animation-duration: 5s; 
} */

.character--name{
  font-size: 2vw;
  color: var(--character--name-color);
}

.character--img--container{
  /* width: 30vw;
  height: auto; */
  position:relative;
  overflow:hidden;
  cursor: pointer;

  width: 70%;
  height: 70%;
  /* height: auto; */
  margin: 0 auto;
  margin-top: 2rem;
  border-radius: 0.5rem;
}

.character--img--container::before{
  content:"";
  z-index:10;
  position:absolute;
  height:200%;
  width:200%;
  top:-120%;
  left:-120%;
  background:linear-gradient(transparent 0%,
    rgba(255, 255, 255, 0.1) 45%,
    rgba(255, 255, 255, 0.5) 50%,
    rgba(255, 255, 255, 0.1) 55%,
    transparent 100%);
  transition:all 10s;
  transform:rotate(-45deg);
  animation: shine 10s infinite forwards;
}
/* CHARACTERS LIST */


/* BACK BUTTON */
  .button-back{
    height: 4rem;
    width: 4rem;
    margin-left: 2rem;
    cursor: pointer;
    margin-bottom: 1rem;
    margin-top: 1rem;
  }

  .button-back-img{
    height: 100%;
  }
/* BACK BUTTON */


/* CHARACTER WITH HIS/HER INFO */
.character--container{
  width: 100vw;
  height: fit-content;
}

.character--withInfo{
  /* height: 80vh; */ /* EYE WITH THIS */
  width: 80vw;
  background-color: var(--character--withInfo-background);
  margin: 0 auto;
  margin-bottom: 3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
}
.character--withInfo--n-f-a{
  margin-left: 5rem;
}

.character--withInfo--img-container-img{
  /* display:inline-block; */
  width: 20vw;
  height: auto;
  display: flex;
  justify-content: center;
}
.character--withInfo--img{
  /* width: 25%;
  height: 55%; */
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
  margin-top: 1rem;
  cursor: pointer;
}

.character--withInfo--img:hover{
  color: #111; 
  background: rgb(8, 9, 41); 
  animation-name: rainbow;
  animation-iteration-count: infinite;  
  animation-duration: 5s; 
}


@keyframes rainbow {  
  0% { 
    box-shadow: 0 0 20px rgb(255, 0, 0);
    border: 2px solid rgb(255, 0, 0);
  }
  20% { 
    box-shadow: 0 0 20px rgb(255, 238, 0);
    border: 2px solid rgb(255, 238, 0);
  }
  50% { 
    box-shadow: 0 0 20px rgb(0, 255, 0);
    border: 2px solid rgb(0, 255, 0);
  }
  60% { 
    box-shadow: 0 0 20px rgb(0, 0, 255);
    border: 2px solid rgb(0, 0, 255);
  }
  90% { 
    box-shadow: 0 0 20px rgb(255, 0, 179);
    border: 2px solid rgb(255, 0, 179);
  }
  100% { 
    box-shadow: 0 0 20px rgb(255, 0, 0);
    border: 2px solid rgb(255, 0, 0);
  }
}

@keyframes rainbow2 {  
  0% { 
    /* box-shadow: 0 0 100px rgb(255, 0, 0); */
    padding: 0.5rem solid rgba(255, 0, 0, 0.205);
  }
  20% { 
    /* box-shadow: 0 0 100px rgb(255, 238, 0); */
    padding: 0.5rem solid rgba(255, 238, 0, 0.144);
  }
  50% { 
    /* box-shadow: 0 0 100px rgb(0, 255, 0); */
    padding: 0.5rem solid rgba(0, 255, 0, 0.253);
  }
  60% { 
    /* box-shadow: 0 0 100px rgb(0, 0, 255); */
    padding: 0.5rem solid rgba(0, 0, 255, 0.233);
  }
  90% { 
    /* box-shadow: 0 0 100px rgb(255, 0, 179); */
    padding: 0.5rem solid rgba(255, 0, 179, 0.164);
  }
  100% { 
    /* box-shadow: 0 0 100px rgb(255, 0, 0); */
    padding: 0.5rem solid rgba(255, 0, 0, 0.116);
  }
}

.character--withInfo--img-zoomed{
  /* position:absolute; */
  /* top:0;
  left:0; */
  height:100%;
  width: 100%;
  border-radius: 0.5rem;
  animation-name: grow;
  animation-iteration-count: 1;  
  animation-duration: 3s; 
}

.character--withInfo--img-container{
  /* display:inline-block; */
  width: 30vw;
  height: auto;
  position:relative;
  overflow:hidden;
  cursor: pointer;
  display: flex;
  justify-content:center;

}
.character--withInfo--img-container::before{
  content:"";
  z-index:10;
  position:absolute;
  height:200%;
  width:200%;
  top:-120%;
  left:-120%;
  background:linear-gradient(transparent 0%,
    rgba(255, 255, 255, 0.1) 45%,
    rgba(255, 255, 255, 0.5) 50%,
    rgba(255, 255, 255, 0.1) 55%,
    transparent 100%);
  transition:all 50s;
  transform:rotate(-45deg);
  animation: shine 20s infinite forwards;
}
  
@keyframes grow {  
  /* 0% { 
    height:10%;
    width: 20%;
  }
  20% { 
    height:30%;
    width: 25%;
  }
  50% { 
    height:40%;
    width: 35%;
  }
  60% { 
    height: 50%;
    width: 40%;
  }
  90% { 
    height: 80%;
    width: 70%;
  }
  100% { 
    height:100%;
    width: 100%;
  } */
  from {
    transform: scale(0.5);
  }
  to {
    transform: scale(1);
  }
}

@keyframes shine{
  0%{top:-120%;left:-120%;}
  /* 10%{left:100%;top:100%;}
  20%{left:100%;top:100%;} */
  100%{left:100%;top:100%;}
  
}

.character--withInfo--name{
  font-size: 2vw;
  font-weight: 800;
  color: var(--character--withInfo--name-color);
}

.character--withInfo--fullname{
  font-size: 2vw;
  font-weight: 800;
  color: var(--character--withInfo--fullname-color);
}

.character--withInfo--alignment{
  font-size: 2vw;
  font-weight: 800;
  color: var(--character--withInfo--alignment-color);
}

.character--withInfo--publisher{
  font-size: 2vw;
  font-weight: 800;
  color: var(--character--withInfo--alignment-color);
}


/* CAROUSEL */
/* .react-3d-carousel > .slider-container > .slider-right > div {
  border: 2px solid #ffffff;
  display: none;
  border: none;
} */
.carousel--img{
  /* height: auto; */
  /* width: 30vw; */
  /* justify-self: center; */
  height:100%;
  width:100%;
  border-radius: 0.5rem;
}

.carousel--container{
  height: auto;
  width: 30vw;
  border-radius: 0.5rem;
  position:relative;
  overflow:hidden;
}

.carousel--container::before{
  content:"";
  z-index:10;
  position:absolute;
  height:200%;
  width:200%;
  top:-120%;
  left:-120%;
  background:linear-gradient(transparent 0%,
    rgba(255, 255, 255, 0.1) 45%,
    rgba(255, 255, 255, 0.5) 50%,
    rgba(255, 255, 255, 0.1) 55%,
    transparent 100%);
  transition:all 10s;
  transform:rotate(-45deg);
  animation: shine 10s infinite forwards;
  /* animation-delay: 150s; */
}


.slider-right > div {
  border: 5px solid #ffffff;
  display: none;
  border: none;
}

.slider-left > div {
  border: 5px solid #ffffff;
  display: none;
  border: none;
}

.react-3d-carousel{
  margin-bottom: 8rem;
}
/* CAROUSEL */


/* ACTUAL INFO */

.character--withInfo--info{
  /* height: 80vh; */ /* EYE WITH THIS */
  width: 80vw;
  margin: 0 auto;
  margin-bottom: 4rem;
  padding: 0 0 2rem 0;
  border-radius: 0.5rem;

  background-color: var(--character--withInfo--info-background);
}

.character--withInfo--statSelectors{
  width: 100%;
  height: 25%;
  display: grid;
  grid-template-columns: repeat(3, 30%);
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  border-radius: 0.5rem 0.5rem 0 0;
  background-color: var(--character--withInfo--statSelectors-background);
}

.statSelectors{
  color: var(--statSelectors-color);
  font-size: 3vw;
  cursor: pointer;
} 

.statSelectors:hover{
  color: var(--statSelectors-selected-color);
  font-size: 3vw;
}

.statSelectors-selected{
  color: var(--statSelectors-selected-color);
  font-size: 3vw;
  cursor: pointer;
  border-bottom: 0.3rem solid blue;
}

/* .character--withInfo--infoByStat{

} */

/* .character--withInfo--infoByStat--stat{

} */

.stat{
  width: 85%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin: 0 auto;
}

.stat--logo{
  height: 5vw;
  /* display: none; */
}

.stat--info{
  color: var(--stat-info-color);
  font-size: 2vw;
}

.stat-publisher{
  display: flex;
  justify-content: center;
}

.stat-publisher-logo{
  width: 45vw;
  height: auto;
  border-radius: 0.5rem;
}
/* ACTUAL INFO */
/* CHARACTER WITH HIS/HER INFO */


